import React, { useEffect, useState, useContext, useMemo } from "react";
import { Link, __RouterContext } from "react-router-dom";
import { getMenu } from "../../mockData/config";
// import { changeLanguage } from "../redux/actions/lang";
import { connect } from "react-redux";
import HomeItem from "./HomeItem";
import RoomsItem from "./RoomsItem";
import ServicesItem from "./ServicesItem";
import PackagesItem from "./PackagesItem";
import GalleryItem from "./GalleryItem";
import AboutItem from "./AboutItem";
import ContactItem from "./ContactItem";
import TopBar from "./TopBar";

const Menu = ({ transparent, dispatch, lang }) => {
  const RouterContext = useContext(__RouterContext);
  const [menu, setMenu] = useState("Travern");
  // const [currentLang, setCurrentLang] = useState(lang);
  const { location } = RouterContext;
  const pathname = useMemo(() => location.pathname, [location.pathname]);

  // useEffect(() => {
  //   if (lang === "vn") {
  //     return setCurrentLang("en");
  //   }
  //   return setCurrentLang("vn");
  // }, [lang]);

  const style = {
    backgroundColor: transparent ? "transparent" : "#3b2c18"
  };

  useEffect(() => {
    getMenu().then(item => {
      const data = item.data();
      setMenu(data);
    });
  }, []);

  return (
    <div>
      <div className="top-header">
        <div className="container">
          <TopBar />
          <div className="clear" />
        </div>
      </div>

      <header className="header" style={style}>
        <div className="container mob-padding">
          <div className="logo">
            <a href="#muine">
              <h1>{menu && menu.title && menu.title[lang]} </h1>
            </a>
            <p> {menu && menu.subTitle && menu.subTitle[lang]}</p>
          </div>

          <div className="toggle">
            <a className="toggleMenu" href="#muine" style={{ display: "none" }}>
              {" "}
            </a>
          </div>

          <div className="head-button">
            <Link to="/booking" className="hover-link">
              {menu && menu.book && menu.book[lang]}
            </Link>
          </div>

          <div className="sitenav">
            <div className="menu-main-menu-container">
              <ul id="menu-main-menu" className="menu">
                <HomeItem
                  name={menu && menu.home && menu.home[lang]}
                  pathname={pathname}
                />
                {/* Menu with sub menu */}
                <RoomsItem
                  name={menu && menu.room && menu.room[lang]}
                  pathname={pathname}
                />

                <ServicesItem
                  name={menu && menu.services && menu.services[lang]}
                  pathname={pathname}
                />

                <PackagesItem
                  name={menu && menu.packages && menu.packages[lang]}
                  pathname={pathname}
                />

                <GalleryItem
                  name={menu && menu.gallery && menu.gallery[lang]}
                  pathname={pathname}
                />

                <AboutItem
                  name={menu && menu.about && menu.about[lang]}
                  pathname={pathname}
                />

                <ContactItem
                  name={menu && menu.contact && menu.contact[lang]}
                  pathname={pathname}
                />
              </ul>
            </div>
          </div>
          <div className="clear" />
        </div>
      </header>
    </div>
  );
};

export default connect(state => {
  return {
    lang: state.lang.currentLang
  };
})(Menu);
