import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { changeLanguage } from "../../redux/actions/lang";

const TopBar = ({ dispatch, lang }) => {
  const [currentLang, setCurrentLang] = useState(lang);

  useEffect(() => {
    if (lang === "vn") {
      return setCurrentLang("en");
    }
    return setCurrentLang("vn");
  }, [lang]);

  return (
    <>
      <div className="tp-header-left">
        <ul>
          <li className="iconadd">
            Khu pho 14, Mui Ne Ward , Phan Thiet City, Vietnam
          </li>
          <li className="iconnum">
            <a href="tel:+888 888 8888">+84.252 222 0222</a>
          </li>
        </ul>
      </div>

      <div className="tp-header-right">
        <div className="social-icons">
          <a
            style={{ backgroundColor: "" }}
            href="https://www.facebook.com/ThuyTrieuTran1235"
            target="_blank"
            rel="noopener noreferrer"
            title="facebook"
          >
            <i className="fab fa-facebook-f"></i>
          </a>

          <a
            style={{ backgroundColor: "" }}
            href="https://www.facebook.com/ThuyTrieuTran1235"
            target="_blank"
            rel="noopener noreferrer"
            title="twitter"
          >
            <i className="fab fa-twitter"></i>
          </a>
          <a
            style={{ backgroundColor: "" }}
            href="https://www.facebook.com/ThuyTrieuTran1235"
            target="_blank"
            rel="noopener noreferrer"
            title="instagram"
          >
            <i className="fab fa-instagram"></i>
          </a>
          <a
            style={{ backgroundColor: "" }}
            href="#muine"
            title="language"
            onClick={() => dispatch(changeLanguage(currentLang))}
          >
            <i className="fa fa-globe-asia" />
          </a>
        </div>{" "}
      </div>
    </>
  );
};

export default connect(state => {
  return {
    lang: state.lang.currentLang
  };
})(TopBar);
