import React, { useEffect, useState } from "react";
import { getContact } from "../../mockData/config";
import { connect } from "react-redux";
import Menu from "../MenuItem/Menu";
import Banner from "../Common/Banner";
import ContentContainer from "./ContentContainer";
import FormContainer from "./FormContainer";

const Contact = ({ transparent, dispatch, lang }) => {
  const [contact, setContact] = useState("Travern");

  useEffect(() => {
    getContact().then(item => {
      const data = item.data();
      setContact(data);
    });
  }, []);

  return (
    <div>
      <Menu />
      <Banner
        name={contact && contact.page_title && contact.page_title[lang]}
      />

      <div className="content-area">
        <div className="middle-align">
          <div className="site-main sitefull">
            <div className="container">
              <div className="contact-content">
                <ContentContainer
                  title={
                    contact &&
                    contact.contact_title &&
                    contact.contact_title[lang]
                  }
                  description={
                    contact && contact.description && contact.description[lang]
                  }
                  addressTitle={
                    contact &&
                    contact.address_title &&
                    contact.address_title[lang]
                  }
                  address={contact && contact.address && contact.address[lang]}
                  email={contact.email}
                  phoneTitle={
                    contact && contact.phone_title && contact.phone_title[lang]
                  }
                  phone={contact.phone}
                  fax={contact.fax}
                />
                <FormContainer
                  namePlaceHolder={
                    contact &&
                    contact.name_placeholder &&
                    contact.name_placeholder[lang]
                  }
                  messagePlaceholder={
                    contact &&
                    contact.message_placeholder &&
                    contact.message_placeholder[lang]
                  }
                  sendInfo={
                    contact && contact.send_info && contact.send_info[lang]
                  }
                />
                <div className="clear" />
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="gmap">
        <iframe
          title="MuiNe Map"
          // className="blackmap"
          src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3919.349693762409!2d106.66656495053068!3d10.784506261955404!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x31752ed141f27173%3A0xa568b8633f8c1c36!2zNjAxL0EgQ8OhY2ggTeG6oW5nIFRow6FuZyBUw6FtLCBQaMaw4budbmcgMTUsIFF14bqtbiAxMCwgSOG7kyBDaMOtIE1pbmgsIFZpZXRuYW0!5e0!3m2!1sen!2s!4v1577163883630!5m2!1sen!2s"
          width="98%"
          height="600"
          frameborder="0"
        />
      </div>

      <div class="clear" />
    </div>
  );
};

export default connect(state => {
  return {
    lang: state.lang.currentLang
  };
})(Contact);
